@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
  background-color: rgb(0,40,50);
  display: grid;
  position: relative;
  max-width: 100%;
  grid-template-columns: 30px 320px;
  grid-template-rows: auto;
  grid-auto-columns: 320px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 20px;
  padding: 0;
  margin: 20px 0 20px 0;
  max-width: 800px;
  font-family: 'Inconsolata', monospace;
}

Nav {
  margin-top: 125px;
  grid-row: 2 / span 1;
  align-self: start;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}

.Overlay {
  grid-row: 2 / -1;
  grid-column: 2 / -1;
  z-index: 1;
  position: -webkit-sticky;
  position: sticky;
  top: 10%;
  width: 100%;
}

.EditorGrid {
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
}

.ButtonPanel {
  grid-row: 2 / span 1;
  grid-column: 3 / span 1;
}

.Container {
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border: 6px solid rgb(0,55,65);
  background-color: rgb(0,30,40);
  padding: 10px;
  grid-column: 2 / 4;
  box-shadow: 5px 5px 10px 3px rgb(0, 20, 0, 0.5);
  color: rgb(0,190,200);
}

.Title {
  grid-row: 1 / span 1;
  grid-column: 2 / 4;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.ToggleReference {
  /* position: relative; */
  position: -webkit-sticky;
  position: sticky;
  grid-column: 2 / 4;
}

@media screen and (max-width: 726px) {
  .App {
    grid-template-columns: 320px;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }

  Nav {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    position: static;
    margin: 0;
  }

  .EditorGrid {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
  }

  .ButtonPanel {
    grid-column: 1 / span 1;
    grid-row: 3 / span 1;
  }

  .Container {
    grid-column: 1 / span 1;
  }

  .ToggleReference {
    grid-column: 1 / span 1;
  }

  .Title {
    grid-row: 1 / span 1;
    grid-column: 1 / span 1;
  }

}

.Container__title {
  font-size: 14px;
  text-align: center;
  background-color: rgba(21, 21, 21);
  border: 6px solid rgb(0,55,65);
  width: 95%;
  padding: 10px;
  margin: -40px 0 15px 0;
  border-radius: 25px;
  box-shadow: 5px 5px 5px 3px rgb(0, 20, 0, 0.3);
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  background-color: rgb(0,40,50);
  color: rgb(240, 10, 0);
}

.flex-jc-aic {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.w100 {
  width: 100%;
}

.mh300 {
  min-height: 300px;
}

.bs {
  box-shadow: 0 0 500px 500px rgba(0,5,15,0.85);
}

.Overlay span, a {
  background-color: rgb(200,0,100);
  color: black;
  padding: 5px 10px 5px 10px;
  border-radius: 20px;
}

a {
  background-color: rgb(0,180,0);
  color: black;
  margin: 10px;
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  background-color: rgb(0,250,0);
}

